.header{
    background-color: #131921;
    display: flex;
    align-items: center;
    position: sticky;
    top: 0;
    z-index: 1;

}
.header_logo{
    width: 100px;
    object-fit: contain;
    margin-top: 18px;
    margin-right: 20px;
    margin-left: 20px;

}
.header__search{
    display: flex;
    flex:1px;

}
.header__searchIcon{
    background-color: #cd9042;
    padding: 5px;
    height: 22px;

}
.header__searchInput{
    height: 12px;
    padding: 10px;
    border: none;
    width: 100%;

}
.header__nav{
    display: flex;
    justify-content: space-evenly;

}
.header__link{
    color: white;
    text-decoration: none;
}
.header__option{
    display: flex;
    flex-direction: column;
    margin-left: 10px;
    margin-right: 10px;
}
.header__optionLineOne{
    font-size: 10px;
}
.header__optionLineTwo{
    font-size: 13px;
    font-weight: 800; 
}
.header__optionBasket{
    display: flex;
    align-items:center;
}
.header__basketcount{
    margin-left: 10px;
    margin-right: 10px;
}